%header-background {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

%header-clean {
  margin: 0;
  padding: 0;
}

%list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

%link {
  display: block;
  text-decoration: none;
}
